.el-rate{
    height: 20px;
    line-height: 1;
}

.el-rate__item{
    display: inline-block;
    position: relative;
    font-size: 0;
    vertical-align: middle;
}

.el-rate__icon{
    position: relative;
    display: inline-block;
    font-size: 18px;
    margin-right: 6px;
    color: rgb(191, 203, 217);
    transition: .3s;
}

.el-rate__icon .path2 {
    position: absolute;
    left: 0;
    top: 0;
}

.el-rate__icon.hover{
    transform: scale(1.15);
}

.el-rate__decimal{
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    overflow: hidden;
}

.el-rate__text{
    font-size: 14px;
    vertical-align: middle;
}
