.el-switch{
    display: inline-block;
    position: relative;
    font-size: 14px;
    line-height: 22px;
    height: 22px;
    vertical-align: middle
}

.el-switch .label-fade-enter, .el-switch .label-fade-leave-active{
    opacity: 0
}

.el-switch.is-disabled .el-switch__core{
    border-color: rgb(228, 232, 241) !important;
    background: rgb(228, 232, 241) !important
}

.el-switch.is-disabled .el-switch__core span{
    background-color: rgb(251, 253, 255) !important
}

.el-switch.is-disabled .el-switch__core ~ .el-switch__label *{
    color: rgb(251, 253, 255) !important
}

.el-switch.is-disabled .el-switch__input:checked + .el-switch__core{
    border-color: rgb(228, 232, 241);
    background-color: rgb(228, 232, 241);
}

.el-switch.is-disabled{}

.el-switch.is-disabled .el-switch__core, .el-switch.is-disabled .el-switch__label{
    cursor: not-allowed
}

.el-switch__label{
    transition: .2s;
    position: absolute;
    z-index: 10;
    width: 46px;
    height: 22px;
    left: 0;
    top: 0;
    display: inline-block;
    font-size: 14px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none
}

.el-switch__label *{
    line-height: 1;
    top: 4px;
    position: absolute;
    font-size: 14px;
    display: inline-block;
    color: #fff
}

.el-switch__label--left i {
    left: 6px;
}

.el-switch__label--right i {
    right: 6px;
}

.el-switch__input{
    display: none
}

.el-switch__input:checked + .el-switch__core{
    border-color: #20a0ff;
    background-color: #20a0ff
}

.el-switch__core{
    margin: 0;
    display: inline-block;
    position: relative;
    width: 46px;
    height: 22px;
    border: 1px solid rgb(191, 203, 217);
    outline: none;
    border-radius: 12px;
    box-sizing: border-box;
    background: rgb(191, 203, 217);
    cursor: pointer;
    transition: border-color .3s, background-color .3s
}

.el-switch__core .el-switch__button{
    top: 0;
    left: 0;
    position: absolute;
    border-radius: 100%;
    transition: transform .3s;
    width: 16px;
    height: 16px;
    z-index: 20;
    background-color: #fff
}

.el-switch--wide .el-switch__label {}

.el-switch--wide .el-switch__label.el-switch__label--left span{
    left: 10px
}

.el-switch--wide .el-switch__label.el-switch__label--right span{
    right: 10px
}
