body {
  background-color: #ffffff;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #636363;
}
body a {
    color: #3884b0;
    text-decoration: none;
    -webkit-transition: color .3s ease 0s;
    transition: color .3s ease 0s;
    outline: none;
}
body a:hover {
      color: #3c8dbc;
      text-decoration: none;
}
body .pull-right {
    float: right;
}
body .pull-left {
    float: left;
}
body .space10 {
    height: 10px;
    width: 100%;
}
body .nopadding {
    padding: 0px;
}
body .aright {
    text-align: right;
}
body .aleft {
    text-align: left;
}
body .acenter {
    text-align: center;
}
body .lborder {
    border-left: 1px solid aliceblue;
}
body .rborder {
    border-right: 1px solid aliceblue;
}
body .tborder {
    border-top: 1px solid aliceblue;
}
body .bborder {
    border-bottom: 1px solid aliceblue;
}
body .displayn {
    display: none;
}
body .displayb {
    display: block;
}
body .vamiddle {
    vertical-align: middle;
}
body .vatop {
    vertical-align: top;
}
body .vabottom {
    vertical-align: bottom;
}
body .color-primary {
    color: #3c8dbc;
}
body img.img-circle {
    border-radius: 50%;
}
body h4 {
    font-size: 20px;
    font-weight: 400;
    margin: 20px 0;
    margin-left: 10px;
}
body b, body strong {
    font-weight: 500;
}
body .form {
    margin: auto;
