body {
  width: 100%;
  height: 100%;
}
body .sidebar {
    background-color: #3c8dbc;
    width: 300px;
    height: auto;
}
body .sidebar header.logo {
      color: white;
      font-size: 20px;
      font-weight: 400;
      background-color: #429bcf;
      height: 65px;
      text-align: center;
      padding-top: 12px;
      padding-left: 50px;
}
body .sidebar header.logo span {
        font-size: 14px;
}
body .sidebar header.logo img {
        width: 47px;
        position: absolute;
        top: 9px;
        left: 15px;
        float: left;
}
body .sidebar .menu-profile {
      border-bottom: 1px solid #429bcf;
}
body .sidebar .menu-profile img {
        width: 150px;
        border: 2px solid #4eb6f2;
        margin-top: 15px;
        margin-bottom: 10px;
}
body .sidebar .menu-profile .fullname {
        color: white;
        font-size: 18px;
        font-weight: 400;
}
body .sidebar .menu-profile button {
        margin: 10px 0;
}
body .sidebar .menu-balance {
      padding: 15px;
      border-bottom: 1px solid #429bcf;
}
body .sidebar .menu-balance .line {
        color: white;
        font-size: 14px;
        padding-bottom: 10px;
}
body .sidebar .side-menu {
      margin-bottom: 60px;
}
body .sidebar .side-menu .line {
        height: 60px;
        line-height: 60px;
        font-size: 16px;
        -webkit-transition: background-color 0.3s;
        transition: background-color 0.3s;
}
body .sidebar .side-menu .line a {
          display: inline-block;
          color: white;
          width: 100%;
          outline: none;
}
body .sidebar .side-menu .line a.item {
            padding-left: 45px;
            float: left;
}
body .sidebar .side-menu .line a.item i {
            width: 12px;
            height: 12px;
            border-radius: 50%;
            border: 2px solid #317399;
            -webkit-transition: border-color 0.3s;
            transition: border-color 0.3s;
            display: block;
            position: absolute;
            margin-top: 25px;
            margin-left: -25px;
}
body .sidebar .side-menu .line .shortcut {
          color: white;
          width: 60px;
          display: inline-block;
          text-align: center;
          font-size: 12px;
          position: absolute;
          margin-left: -60px;
}
body .sidebar .side-menu .line .shortcut i {
            padding: 7px 12px;
            background-color: #3884b0;
            -webkit-transition: background-color 0.3s;
            transition: background-color 0.3s;
            border-radius: 3px;
            font-style: normal;
            font-weight: bold;
}
body .sidebar .side-menu .line .shortcut i.fa {
            font-weight: normal;
}
body .sidebar .side-menu .line:hover,
      body .sidebar .side-menu .line.router-link-active {
        background-color: #3884b0;
        font-weight: 400;
}
body .sidebar .side-menu .line:hover .item i,
        body .sidebar .side-menu .line.router-link-active .item i {
          border-color: #4eb6f2;
}
body .sidebar .side-menu .line:hover .shortcut i,
        body .sidebar .side-menu .line.router-link-active .shortcut i {
          background-color: #429bcf;
}
.slideout-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 256px;
  min-height: 100vh;
  -webkit-overflow-scrolling: touch;
  z-index: 0;
  display: none;
  overflow-y: auto;
}
.slideout-menu-left {
  left: 0;
}
.slideout-menu-right {
  right: 0;
}
.slideout-panel {
  position: relative;
  background-color: #e5e9ec;
  min-height: 100vh;
}
.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}
.slideout-open .slideout-menu {
  display: block;
}
.topbar {
  height: 60px;
  background-color: white;
}
.topbar .icon {
    color: #3c8dbc;
    width: 60px;
    height: 60px;
    text-align: center;
    cursor: pointer;
    -webkit-transition: background-color 0.2s;
    transition: background-color 0.2s;
}
.topbar .icon:hover {
      background-color: #f7f7f7;
}
.topbar .icon i.fa {
      font-size: 30px;
      margin-top: 15px;
}
.topbar .separator {
    width: 1px;
    height: 40px;
    margin-top: 10px;
    background-color: #e5e9ec;
}
.topbar .profile {
    height: 60px;
    padding: 8px 10px;
    line-height: 44px;
    cursor: pointer;
    -webkit-transition: background-color 0.3s;
    transition: background-color 0.3s;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}
.topbar .profile:hover {
      background-color: #f7f7f7;
}
.topbar .profile span {
      display: inline-block;
      font-size: 16px;
      font-weight: 300;
      white-space: nowrap;
}
