.scroller_content::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.scroller_track {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
}
.scroller._scrollbar .scroller_track {
  display: block;
}
.scroller_free {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}
.scroller_bar {
  display: none;
  position: absolute;
  right: 0;
  z-index: 1;
  width: 10px;
  background: #999;
}
.scroller._scrollbar .scroller_bar {
  display: block;
}
.scroller .scroller_track {
  opacity: 0;
  position: absolute;
  z-index: 3;
  top: 2px;
  bottom: 2px;
  right: 2px;
  width: 0;
}
.scroller._scrollbar .scroller_track {
  opacity: 1;
}
.scroller .scroller_bar {
  position: absolute;
  z-index: 1;
  width: 6px;
  border-radius: 3px;
  background: #333;
  opacity: .3;
  -webkit-transition: .1s opacity .4s linear;
  transition: .1s opacity .4s linear;
  pointer-events: auto;
}
.scroller._dragging .scroller_bar {
  /*opacity: 1;*/
}
.scroller .scroller_bar:hover,
.scroller._scrolling .scroller_bar {
  -webkit-transition-duration: 0s;
          transition-duration: 0s;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  opacity: .5;
}
